@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

header {
    width: 100%;
    padding: 10px 38px;
    background-color:white;
    position: fixed;
    box-shadow: 0 2px  #ccc;
    }
  
  .logoContainer {
    display: flex;
    height: 50px;
  }
  .category-item:hover{
      color: white;
      cursor: pointer;
      background-color: blue;
  }
  
  .logo {
    color:blue;
    font-size:16px;
  }
  .logo h2{
      margin-top: 0px;
      margin-bottom: 0px;
      margin-left: 35px;
      font-family: 'Poppins', sans-serif;
      font-weight: 600;
  }
  
  .logo p {
    color: #000;
    font-size: 18px;
    margin: 0px;
    font-weight: 500;
    font-style: italic;
  }
 
  
  .dropdownText {
    cursor: pointer;
    font-size: 15px;
    border: none;
    background-color: #fff;
    padding-left: 90px;
    padding-top:15px;
  }

  
.trending-post-container {
    width: 65%;
    margin: auto;
    padding-top: 100px;
  }

  .platform{
      font-size: 15px;
      color: grey;
      -webkit-transform: translateX(150%);
              transform: translateX(150%);
      margin-top: 5px;
  }

  .head-button{
      font-size: 15px;
      color: white;
      background-color: blue;
      padding:15px 30px;
      margin: 0px;
      margin-top: 2px;
      -webkit-transform: translateX(230%);
              transform: translateX(230%);
      border-radius: 100px;
  }
  
  .heading {
    color: #000;
    margin-right: 680px;
    font-size: 30px;
    font-weight: 600;
    
  }
  
  .categories-container {
    margin-bottom: 20px;
    
  }
  .categories-container >i{
    font-size: "16px";
    color: "#4072bd";

   
  }
  
  .filter-text {
    font-size: 15px;
    font-weight: 600;
    margin-left: 8px;
    padding-right: 680px;
  }
  
  .categories-list {
    margin-top: 12px;
  }
  
  .category-item {
    text-decoration: none;
    font-size: 13px;
    display: inline-block;
    color: #0084ff;
    padding: 6px 14px;
    width: auto;
    border: 1px solid #0084ff;
    margin: 5px;
    border-radius: 15px;
  }

  
.card-wrapper {
    width: 100%;
    margin: auto;
  }
  
  #card-data {
      width: 75%;
      margin-left: 180px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .card-container {
    display: inline-block;
    cursor: pointer;
    overflow: hidden;
    width: calc(280px - 3px);
    height: 460px;
    margin-left: 1%;
    margin-right: 1%;
    margin-bottom: 12px;
    background: #fff;
    border-radius: 6px;
    box-shadow: 0 8px 20px 0 rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(0, 168, 255, 0.3);
  }
  
  .card-content {
    padding: 16px 25px;
  }
  
  .card-img {
    width: 100%;
    height: 176px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
  }
  
  .auth-wrapper {
    margin-top: 12px;
    margin-bottom: 12px;
  }
  
  .card-author {
    color: #0096ff;
    font-size: 16px;
    font-style: Italic;
    font-weight: 500;
  }

  .card-date {
    color: rgba(0, 0, 0, 0.7);
    font-size: 12px;
    font-weight: 300;
  }
  
  .card-title {
    font-size: 15px;
    text-align: left;
    font-weight: 600;
    margin: 0;
  }
  
  .card-desc {
    font-size: 15px;
    font-weight: 400;
    line-height: 1.43;
    color: rgba(0, 0, 0, 0.6);
    margin: 4px 0;
  
  }
  .footer {

    background-color: #172146;
    padding: 15px 16px;
    display: flex;
    
  }
  .footer h2{
    font-family: 'Poppins', sans-serif;
    font-weight: 700;
    margin-bottom: 0px;
    padding: 0px;

  }
  .footer p{
      margin: 0px;
  }
  .foot1{
      width: 20%;
      color: white;
      padding-left: 0px;
      margin-left: 0px; 

  }
  .foot2{
    width: 25%;
    color: white;

}
.foot3{
    width: 25%;
    color: white;

}

.foot4{
    width: 20%;
    color: white;

}
 
  
  
  
